import React from 'react'

const NavBarFooter = (props) => {

    return (
        <div id="footer" className="flex-row" style={{ borderTop: "1px solid #ccc", padding: "5px 0px 5px 0px" }}>
            <div className="navigation-link" style={{ flex: 1 }}>
                <a href="rules.php"></a>
                <span className="separator">|</span>
                <a href="about.php"></a>
                <span className="separator">|</span>
                <b><a href="ad.php">  </a>  </b>
                <span className="separator">|</span>
                <a href={`${window.hash}/find.php`}></a>
                <span className="separator">|</span>
                <noindex>
                    <a rel="nofollow" href={`${window.hash}/sections_list.php`} ></a>
                    <span className="separator">|</span>
                    <a rel="nofollow" href={`${window.hash}/rating.php`}></a>
                    <span className="separator">|</span>
                    <a href="http://kb.mista.ru"> </a>
                    <span className="separator">|</span>
                    <a href="http://wiki.mista.ru">- (2)</a>
                    <span className="separator">|</span>
                    <a rel="nofollow" href="http://m.mista.ru/"></a>
                    <span className="separator">|</span>
                    <a href="archive.php"></a>
                    <span className="separator">|</span>
                    <a href="moders.php"></a>
                    <span className="separator">|</span>
                    <a href="users_gallery.php"></a>
                    <span className="separator">|</span>
                    <span>18+</span>
                </noindex>
            </div>

        </div>
    )
}

export default NavBarFooter;


